	const [trans, setTrans] = useState();
	const [rec, setRec] = useState(true);
	// const [transcript, setTranscript] = useState();

	// console.log(userId);
	useEffect(() => {
		db.collection("users").doc(user.phoneNumber).collection("profiles");
		// .doc(`${cont}`)
	});

	const submit = (e) => {
		e.preventDefault();

		db.collection("users")
			.doc(`${user.phoneNumber}`)
			// .collection("/userchats/")
			.collection("/chats/")
			.doc(`${"+" + value + cont}`)
			.set({
				// .add({
				id: `${"+" + value + cont}`,
				name: `${"+" + value + cont}`,
				display: password,
				timestamp: firebase.firestore.FieldValue.serverTimestamp(),
			});

		setPassword("");
		setCont("");
	};

	const back = (e) => {
		e.preventDefault();
		history.push("/Contacts");
	};

	///////////////////////////////////////
	const SpeechRecognition =
		window.SpeechRecognition || window.webkitSpeechRecognition;
	const recognition = new SpeechRecognition();
	// var recognition = mySpeechRecognition.continuous;
	recognition.continuous = rec;

	recognition.onresult = function (event) {
		var transcript = Array.from(event.results)
			.map((result) => result[0])
			.map((result) => result.transcript)
			.join("");
		// console.log(transcript);
		if (transcript === "number") {
			setRec(true);
			readOutNumber();
			// setCont("");
		}
		if (transcript === "name") {
			readOutShell();
			setRec(true);
			// readOutName();
			// setPassword("");
		}
		if (transcript.includes("yes")) {
			setRec(true);
			// readOutName();
			// setPassword("");
		}
		if (transcript === "add contact") {
			submitContact();
		}
		if (transcript.includes("add contact")) {
			submitContact();
		}
		if (transcript.includes("clear phone")) {
			setCont("");
		}
		if (transcript.includes("clear name")) {
			setPassword("");
		}
		if (transcript.includes("code India")) {
			setValue(91);
		}
		if (transcript.includes("India")) {
			setValue(91);
		}
		if (transcript === "code India") {
			setValue(91);
		}
		if (transcript === "India") {
			setValue(91);
		}
		if (transcript.includes("set number")) {
			// console.log("set con done");
			var num = transcript.replace(/\D/g, "");
			// console.log(num, "num");
			setCont(num);
		}

		if (transcript.includes("set name")) {
			// console.log("set name done");
			var sli = transcript.slice().split(" ");
			// console.log(sli);
			let match = sli.findIndex(
				(item) => item.toLocaleLowerCase().indexOf("yes") > -1
			);
			// console.log(match);

			let ma = sli.findIndex(
				(item) => item.toLocaleLowerCase().indexOf("set") > -1
			);
			// console.log(ma, "Ma");

			var add = sli.slice(match + 1, ma - 1);
			// console.log(add, "add");
			setPassword(add);
		}
		// if (transcript.includes("set")) {
		// 	console.log("set name done");
		// 	var sli = transcript.slice().split(" ");
		// 	console.log(sli);
		// 	let match = sli.findIndex(
		// 		(item) => item.toLocaleLowerCase().indexOf("name") > -1
		// 	);
		// 	console.log(match);

		// 	let ma = sli.findIndex(
		// 		(item) => item.toLocaleLowerCase().indexOf("set") > -1
		// 	);
		// 	console.log(ma, "Ma");

		// 	let add = sli.slice(match + 1, ma - 1);
		// 	console.log(add, "add");
		// 	setPassword(add);
		// }
		if (transcript.includes("stop")) {
			recognition.stop();
			setRec(false);
		} else if (transcript === "back") {
			history.push("/");
			recognition.stop();
			setRec(false);
		} else if (transcript == "open contacts") {
			history.push("/contacts");
			recognition.stop();
			setRec(false);
		}
		recognition.onerror = (event) => {
			// console.log(event.error);
		};
		// console.log(transcript.includes("add contact"));
		setTrans(transcript);
	};
